@import url(https://fonts.googleapis.com/css?family=Comfortaa:400,700,300);

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.clr-white{
  color: white;
}
.main{
  padding: 30px;
  height: 65vh;
  background-image: url("images/burger.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.main p{
  text-align: center;
  margin: 20px 20px;
  width: 20%;
  color: white;
}
.main-h1{
  text-shadow: 2px 2px 8px gold;
  margin: 0 20px;
}
.api-image{
  border-radius: 50px;
  /* width: 300px; */
  object-fit: fill;
  max-width: 200px;
  max-height: 200px;
  padding: 3px;
  border: 2px solid white;
 
}
.filtered-list{
  width: 200px;
  color: white;
  margin: 30px auto;
  list-style: none;
}
.filtered-list img{
  border-radius: 50px;
  /* width: 300px; */
  object-fit: fill;
  max-width: 200px;
  max-height: 200px;
  padding: 3px;
  border: 2px solid white;
}
.filtered-div{
  margin-top: 20px;
  background-color: #1c0400;

}
.api-div{
  margin: 0 auto;
  margin-top: 40px;
}
.api-div h4{
  margin-bottom: 20px;
}
.order-bt{
  padding: 10px;
  color: rgb(0, 0, 0);
  background-color: rgb(236, 232, 231);
  border: 2px solid black;
  border-radius: 8px;
}
.order-bt:hover{
  cursor: pointer;
  font-weight: bold;
  background-color: #f88c04;
  color: rgb(255, 255, 255);
}
.api-items{
  background-color: #1c0400;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}
.ul-filtered{
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}
.marg-top{
  margin-top: 10px;
}
.dish-div{
  background-color: white;
  ;
}
.dish-div h1,p{
  color: rgb(0, 0, 0);
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
}
.category-list{
  text-align: center;
  width: 120px;
  font-weight: bold;
  border-radius: 20px;
  list-style: none;
  padding: 10px;
  background-color: gold;
}
.category-list:hover{
  cursor: pointer;
  background-color: black;
  color: white;
}
.ul-category{
  margin: 0 auto;
  max-width: 700px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-around;
}
.categ-secDic{
  padding: 20px;
  text-align: center;
}

.loading-div h1{
  text-align: center;
}
.active{
  color: red;
}
.alert{
  color: white;
  margin: 0 auto;
}
.ul-pagination{
  padding: 20px;
  display: flex;
  /* justify-content: space-around; */
  justify-content: center;
}
.li-pagination li{
  text-align: center;
  font-weight: bold;
  margin-right: 10px;
  border: 1px solid black;
  list-style: none;
  width: 20px;
}
.li-pagination li:hover{
  cursor: pointer;
  background-color: gold;
  color: black;
}